.cartitems {
  max-width: 1440px;
  margin: 50px auto;
}
.cartitems hr {
  height: 3px;
  background-color: #e2e2e2;
  border: 0;
}
.cartitems-format-main {
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
  align-items: center;
  gap: 75px;
  padding: 10px 0;
  color: #454545;
}
.cartitems-format {
  font-size: 17px;
  font-weight: 500;
}
.carticon-product-icon {
  height: 62px;
}
.cartitems-remove-icon {
  width: 15px;
  margin: 0px 30px;
  cursor: pointer;
}
.cartitems-quantity {
  width: 64px;
  height: 50px;
  border: 2px solid #ebebeb;
  background-color: #fff;
}
.cartitems-down {
  display: flex;
  margin: 50px 0;
}
.cartitems-total {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-right: 150px;
  gap: 20px;
}
.cartitems-total-item {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
}
.cartitems-total button {
  width: 262px;
  height: 58px;
  outline: none;
  border: none;
  background: #ff5a5a;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}
.cartitems-promocode {
  flex: 1;
  font-size: 16px;
  font-weight: 500;
}
.cartitems-promocode p {
  color: #555;
}
.cartitems-promobox {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  padding-left: 15px;
  height: 58px;
  background-color: #eaeaea;
}
.cartitems-promobox input {
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 16px;
  width: 330px;
  height: 50px;
}
.cartitems-promobox button {
  width: 170px;
  height: 58px;
  font-size: 16px;
  background-color: black;
  color: white;
  cursor: pointer;
}
